import tagData from "../../assets/tagData"
import $ from "jquery"
export default {
  name: 'taglist',
  props:{
    catalogName:{
      type:String,
      default:""
    },
    isExpress:{  // 是否有展开功能 (true有，false无)
      type:Boolean,
      default:false
    },
    selectId:{
      type:String,
      default:null
    }
  },
  data(){
    return {
      tagsData:tagData[this.catalogName],
      showToggle:false // 默认隐藏
    }
  },
  computed:{
    tagShow(){
      if(this.isExpress){ // 有展开功能
        if(this.showToggle){ //
          return true
        }else{
          return false
        }
      }else{  // 无展开功能
        return true // 全部显示
      }
    }
  },
  components:{
     //Tag
  },
  methods:{
    ExpressClickHandle(){
      this.showToggle=!this.showToggle
      return this.showToggle
    },
    TagClickHandle(){
      let catalogName=event.target.getAttribute('catalogName');
      let tId=event.target.getAttribute('tId');
      if(this.isExpress){
        this.$store.dispatch("modifyStateType",{catalogName:catalogName,tId:tId})
      }else{
        this.$store.dispatch("modifyStateType",{catalogName:catalogName,tId:tId})
        //console.log( this.$store.state.selectM[catalogName] )
        this.$nextTick(()=>{
          this.$router.push({path:'/#Result'})
        })
      }
    }

  },
  mounted(){

  }
}



